import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import './index.css'
import routes from './router'


Vue.use(VueRouter)
Vue.config.productionTip = false


const router = new VueRouter({
    routes,
    linkExactActiveClass: 'active',
    linkActiveClass: '',
    mode: "history",
})

router.beforeEach((to, from, next) => {
    next();
})
new Vue({
    component: {},
    router,
    render: h => h(App),
}).$mount('#app')
