const routes = [
    {
        path: "/",
        redirect: {path: '/home'}
    },
    {
        path: "/home",
        name: "home",
        meta: {title: 'Pham Duc Huy - Home Page - '},
        component: () => import(/* webpackChunkName: "Home" */ '../components/Home'),

    },
    {
        path: '/about',
        name: "about",
        meta: {title: 'Pham Duc Huy - About - '},
        component: () => import(/* webpackChunkName: "About" */"../components/AboutPage")
    }
]
export default routes;
