<template>
    <div class="main-container">
        <img src="/images/background.jpg"
             id="image--background"
             class="object-cover absolute w-full h-full"
             alt="">
        <simplebar
            data-simplebar-auto-hide="true"
            class="flex flex-col h-full w-full"
            @scroll="scrollHandler"
            id="simplebarScroller">
            <nav ref="nav"
                 :class="[isSticky?'sticky':'relative']"
                 role="navigation">
                <ul class="flex justify-around md:justify-evenly nav">
                    <li class="py-8 transition"
                        v-for="route in $router.options.routes.filter(item => item.name)"
                        :key="route.name">
                        <router-link :to="route.path" class="nav-link">
                            {{ labels[route.name][lang] }}
                        </router-link>
                    </li>
                </ul>
            </nav>
            <transition name="fade" mode="out-in">
              <keep-alive>
                <router-view :key="$route.path" :lang="lang" :isSticky="isSticky"
                             @changeLanguage="changeLanguage"/>
              </keep-alive>
            </transition>
        </simplebar>
    </div>
</template>

<script>
document.onkeydown = function (e) {
    if ((e.ctrlKey && e.shiftKey && [67, 73, 74, 85].find(item => item == e.keyCode)) || e.keyCode == 123) {
        return false;
    }
}

import simplebar from "simplebar-vue";
import 'simplebar/dist/simplebar.min.css';
import data from "./components/lang";

export default {
    name: 'App',
    components: {
        simplebar
    },
    data() {
        return {
            isSticky: false,
            ...data,
            lang: null,
        }
    },
    methods: {
        scrollHandler: function () {
            this.isSticky = document.querySelector(".simplebar-content-wrapper").scrollTop;
        },
        easterEgg: function () {
            console.log("%cHey hello there! :)", "color:red; font-size: 30px;")
            console.log("You have found an Easter Egg. Well done!")
        },
        runningTitle: function () {
            let text = "Pham Duc Huy - Portfolio - ";
            if (text) {
                setInterval(() => {
                    let firstSpaceIndex = text.indexOf(' ') + 1;
                    text = text.substr(firstSpaceIndex) + text.substr(0, firstSpaceIndex);
                    window.document.title = text;
                }, 1000);
            }
        },
        setLanguage: function (key = '') {
            this.lang = key.length ? key : this.languages[0]['key'];
        },

        changeLanguage: function() {
            this.setLanguage(this.languages.find((item) => item['key']!=this.lang)['key'])
        }
    },
    mounted: function () {
        this.easterEgg()
        this.runningTitle()
        this.setLanguage(this.languages[0]['key'])
    },

    updated: function () {
    },
}
</script>

<style>
.fade-enter-active, .fade-leave-active {
    transition: all .5s;
}

.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */
{
    opacity: 0;
}

#container {

}

#background {
}

.simplebar-content-wrapper {
}
</style>
